import React, {useState, useContext} from 'react'
import RadioCustom from '../tools/Custom/RadioCustom'
import ColorCustom from '../tools/Custom/ColorCustom'
import SingleColorCustom from '../tools/Custom/SingleColorCustom'
import InputCustom from '../tools/Custom/InputCustom'
import { MyContext } from '../../../../pages/Make/MakePageV2'
import SelectCustom from '../tools/Custom/SelectCustom'
import './EditSetting.css'
import '../EditTemplates/Edit.css'
import OpenCloseCustom from '../tools/Custom/OpenCloseCustom'
import OnOffCustom from '../tools/Custom/OnOffCustom'
import produce from 'immer';
import AnimationCustom from '../tools/Custom/AnimationCustom'
import AddContentImg from '../tools/func/FuncContentImg'
import AddFaviconImg from '../tools/func/FuncFaviconImg'
import {Check} from '@styled-icons/bootstrap'
import {ChevronRight} from '@styled-icons/boxicons-regular'
import OverflowScrolling from 'react-overflow-scrolling';
import {
    ChakraProvider,
    Popover,
    PopoverTrigger,
    PopoverContent,
    PopoverHeader,
    PopoverBody,
    PopoverFooter,
    PopoverArrow,
    PopoverCloseButton,
    PopoverAnchor,
    ButtonGroup,
    Portal,
    Button
  } from '@chakra-ui/react'

const fontOptions = [
    { label: '', value: 'Noto Sans KR' },
    { label: '', value: 'Pretendard-Regular' },
    { label: ' ', value: 'NanumSquareRound' },
    { label: ' ', value: 'ROKAFSansBold' },
    { label: ' ', value: 'GmarketSansMedium' },
    { label: ' ', value: 'GowunDodum-Regular' },
    { label: ' ', value: 'S-CoreDream-4Regular' },
    { label: '', value : 'SF_HambakSnow'},
    { label: '24 ', value: 'Cafe24Ssurround'},
    { label: '-Black', value:'LeferiPoint-BlackA'},
    { label: '', value : 'GowunBatang-Regular'},
    { label: ' ', value: 'yg-jalnan'},
]

const shapeOptions = [
    { label: '', value: 0 },
    { label: '', value: 5 },
    { label: '', value: 20 },
]

const sizeOptions = [
    { label: 'Small', value: 50 },
    { label: 'Medium', value: 75 },
    { label: 'Large', value: 100 },
]

function EdtiSetting({setting, setSetting, category}) {
    const [isFontOpen, setIsFontOpen] = useState(false)
    const [isSmallFontOpen, setIsSmallFontOpen] = useState(false)
    const open = () => setIsFontOpen(!isFontOpen)
    const close = () => setIsFontOpen(false)

    const {state, action} = useContext(MyContext) //ContextAPI state action .

    //   
    const animationOptions = [
        {label:'', value: 'none'},
        {label:'', value: 'fade-up'},
        {label:'zoom-in', value: 'zoom-in'}
    ]

    const changeAnimationOption = e => {
        // action.setContents(produce(state.contents, draft => {
        //     draft[state.secNum].animation.type = e;
        // }))
        setSetting({...setting, animation : e})
    }

    //  
    const onChangeContentImage= e => {
        const {target:{files},} = e;
        const oneFile = files[0];
        const reader = new FileReader();
        reader.onloadend = (finishedEvent) => { //     .
            const {currentTarget:{result}} = finishedEvent;
            setSetting(produce(setting, draft=>{
                draft.faviconAttachment = result;             
            }))
        }
        if(oneFile){
            reader.readAsDataURL(oneFile);
        }
    }
    //  
    const RemoveImage = () => {
        setSetting(produce(setting, draft=>{
            draft.faviconAttachment = '';
        }))
    }

    const FontSelect = () => {
        return(
        <div className="edit-element" style={{flexDirection:'row'}}>
            <div className="left" style={{width:'20%'}}></div>
            <div className="right" style={{width:'80%'}}>
                <Popover
                    placement='right'
                    closeOnBlur={false}
                    isOpen={isFontOpen}
                    onClose={close}
                >
                <PopoverTrigger>
                    <div className="font-button" style={{fontFamily: `${setting.font}`}} onClick={() => {open(); setIsSmallFontOpen(false)}}>   , Surfee <ChevronRight size="20" color=""/></div>
                </PopoverTrigger>
                <PopoverContent>
                    <PopoverArrow />

                    <PopoverHeader> .</PopoverHeader>
                    
                    <PopoverCloseButton />
                    
                    <PopoverBody>
                        <div style={{height:'250px'}}>
                        <OverflowScrolling className='overflow-scrolling2'>
                            <div className="font-select__container">
                            {fontOptions.map((item, index) => {
                                return(
                                    <div className={item.value === setting.font ? 'select-hover clicked' : 'select-hover'} onClick={(e) => {setSetting(produce(setting, draft => {
                                        draft.font = item.value
                                    }))}} style={{fontFamily: `${item.value}`}}>
                                        <div className="left">
                                            {item.label}
                                        </div>
                                        {
                                            item.value === setting.font && 
                                            <div className="right">
                                                <Check size="20"/>
                                            </div>
                                        }
                                    </div>
                                )
                            })}
                            </div>
                        </OverflowScrolling>
                        </div>
                    </PopoverBody>
                    
                    <PopoverFooter d='flex' justifyContent='flex-end'>
                        <ButtonGroup size='sm'>
                            <div className="font-done-button" onClick={() => close()}>Apply</div>
                        </ButtonGroup>
                    </PopoverFooter>

                </PopoverContent>
                </Popover>
            </div>
        </div>
        )
    }

    const SmallFontSelect = () => {
        return(
        <div className="edit-element" style={{flexDirection:'row'}}>
            <div className="left" style={{width:'20%'}}></div>
            <div className="right" style={{width:'80%'}}>
                <Popover
                    placement='right'
                    closeOnBlur={false}
                    isOpen={isSmallFontOpen}
                    onClose={() => setIsSmallFontOpen(false)}
                >
                <PopoverTrigger>
                    <div className="font-button" style={{fontFamily: `${setting.smallFont}`}} onClick={() => {setIsSmallFontOpen(!isSmallFontOpen); close()}}>   , Surfee <ChevronRight size="20"/></div>
                </PopoverTrigger>
                <PopoverContent>
                    <PopoverArrow />

                    <PopoverHeader> .</PopoverHeader>
                    
                    <PopoverCloseButton />
                    
                    <PopoverBody>

                        <div style={{height:'250px'}}>
                            <OverflowScrolling className='overflow-scrolling2'>
                                <div className="font-select__container">
                                    {fontOptions.map((item, index) => {
                                        return(
                                            <div className={item.value === setting.smallFont ? 'select-hover clicked' : 'select-hover'} onClick={(e) => {setSetting(produce(setting, draft => {
                                                draft.smallFont = item.value
                                            }))}} style={{fontFamily: `${item.value}`}}>
                                                <div className="left">
                                                    {item.label}
                                                </div>
                                                {
                                                    item.value === setting.smallFont && 
                                                    <div className="right">
                                                        <Check size="20"/>
                                                    </div>
                                                }
                                            </div>
                                        )
                                    })}
                                </div>
                            </OverflowScrolling>
                        </div>
                    </PopoverBody>
                    
                    <PopoverFooter d='flex' justifyContent='flex-end'>
                        <ButtonGroup size='sm'>
                            <div className="font-done-button" onClick={() => setIsSmallFontOpen(false)}>Apply</div>
                        </ButtonGroup>
                    </PopoverFooter>

                </PopoverContent>
                </Popover>
            </div>
        </div>
        )
    }

    const returnTable = () => {
        switch(category){
            case 0:
                // case 0  
                return(
                    <>
                    <div>
                        <OpenCloseCustom title="" use={true} tooltip="       .">
                            <AddFaviconImg text="" subtext=" 1MB  , 64x64px " value={setting.faviconAttachment} func={e => onChangeContentImage(e)} removeFunc={e => RemoveImage(e)}/>
                        </OpenCloseCustom>
                        <OpenCloseCustom title=" " use={true} tooltip="      .">
                            <InputCustom value={setting.title} placeholder="      ." func={(e) => setSetting(produce(setting, draft => {
                                draft.title = e
                            }))} />
                        </OpenCloseCustom>
                        <OpenCloseCustom title="URL" use={true}>
                            <div style={{flexDirection:'column'}}>
                                <div style={{display:'flex'}}>
                                    <div>
                                        <InputCustom value={setting.urlId} placeholder=" url " noKorean func={(e) => setSetting(produce(setting, draft => {
                                            draft.urlId = e;
                                        }))}/>
                                    </div>
                                    <div style={{color:'#202936', marginTop: '30px', marginRight: '30px'}}>
                                        .surfee.co.kr
                                    </div>
                                </div>
                            </div>
                                <div style={{width:'90%', justifyContent:'center', fontSize:'12px', marginBottom:'16px'}}>
                                    *     .
                                </div>
                        </OpenCloseCustom>
                        <OpenCloseCustom title=" " use={true} tooltip="    .        .">
                            <OnOffCustom text="ON/OFF" value={setting.fta.use} func={(e) => setSetting(produce(setting, draft => {
                                draft.fta.use = !setting.fta.use
                            }))} />
                            {
                                setting.fta.use && 
                                <>
                                    <RadioCustom text="" options={sizeOptions} value={setting.fta.size} func={(e) => setSetting(produce(setting, draft => {
                                        draft.fta.size = e
                                    }))} />
                                    <RadioCustom text="" options={shapeOptions} value={setting.fta.shape} func={(e) => setSetting(produce(setting, draft => {
                                        draft.fta.shape = e
                                    }))} />
                                    <div className="edit-element">
                                        {/*     ! */}
                                        <SingleColorCustom text=" " value={setting.fta.backgroundColor} func={(e) => setSetting(produce(setting, draft => {
                                            draft.fta.backgroundColor = e;
                                        }))} />
                                        <div className="edit-element-bar"/>
                                        <SingleColorCustom text=" " value={setting.fta.backgroundColor} func={(e) => setSetting(produce(setting, draft => {
                                            draft.fta.backgroundColor = e;
                                        }))} />
                                    </div>
                                    <OnOffCustom text="" value={setting.ghost.border} func={(e) => setSetting(produce(setting, draft => {
                                        draft.ghost.border = !setting.ghost.border
                                    }))} />
                                    {
                                        setting.ghost.border && 
                                        <ColorCustom text=" " value={setting.ghost.borderColor} func={(e) => setSetting(produce(setting, draft => {
                                            draft.ghost.borderColor = e;
                                        }))} />
                                    }
                                    <OnOffCustom text="" value={setting.ghost.shadow} func={(e) => setSetting(produce(setting, draft => {
                                        draft.ghost.shadow = !setting.ghost.shadow
                                    }))} />
                                    <InputCustom text=" " value={setting.fta.link} placeholder=" " func={(e) => setSetting(produce(setting, draft => {
                                        draft.fta.link = e
                                    }))}/>
                                </>
                            }
                        </OpenCloseCustom>
                    </div>
                    </>
                )

            case 1:
                // case 1  
                return(
                    <>
                    <div>
                        <OpenCloseCustom title=" " tootip="            .">
                            <SingleColorCustom value={setting.color} func={(e) => setSetting(produce(setting, draft => {
                                draft.color = e;
                            }))} />
                        </OpenCloseCustom>
                        <OpenCloseCustom title="" use={true}>
                            <ChakraProvider>
                                {FontSelect()}
                                {SmallFontSelect()}
                            </ChakraProvider>
                        </OpenCloseCustom>
                        <OpenCloseCustom title="CTA " use={true} preseen={
                            <div className="edit-element">
                                <div className="centera" style={{padding:'5px 10px'}}>
                                    <div className="custom-button" style={{ color:`${setting.cta.color}`, border:`${setting.cta.border ? `1px solid ${setting.cta.borderColor}` : 'none'}`, boxShadow:`${setting.cta.shadow ? '2px 2px 5px rgba(0,0,0,0.3)' : 'none'}`, borderRadius:`${setting.cta.borderRadius}px`, backgroundColor:`${setting.cta.backgroundColor}`}}>
                                         
                                    </div>
                                </div>
                            </div>
                        }>
                            <RadioCustom button text="" options={shapeOptions} value={setting.cta.borderRadius} func={(e) => setSetting(produce(setting, draft => {
                                draft.cta.borderRadius = e
                            }))} />
                            <div className="edit-element">
                            <SingleColorCustom text=" " value={setting.cta.backgroundColor} func={(e) => setSetting(produce(setting, draft => {
                                draft.cta.backgroundColor = e;
                            }))} />
                            <div>
                            <div className="edit-element-bar"/>
                            </div>
                            <SingleColorCustom text=" " value={setting.cta.color} func={(e) => setSetting(produce(setting, draft => {
                                draft.cta.color = e;
                            }))} />
                            </div>
                            <OnOffCustom text="" value={setting.cta.border} func={(e) => setSetting(produce(setting, draft => {
                                draft.cta.border = !setting.cta.border
                            }))} />
                                {
                                    setting.cta.border && 
                                        <ColorCustom value={setting.cta.borderColor} func={(e) => setSetting(produce(setting, draft => {
                                            draft.cta.borderColor = e;
                                        }))} />
                                }
                            <OnOffCustom text="" value={setting.cta.shadow} func={(e) => setSetting(produce(setting, draft => {
                                draft.cta.shadow = !setting.cta.shadow
                            }))} />
                        </OpenCloseCustom>
                        <OpenCloseCustom title=" " use={true} preseen={
                            <div className="edit-element">
                                <div className="centera" style={{padding:'5px 10px'}}>
                                    <div className="custom-button" style={{ color:`${setting.ghost.color}`, border:`${setting.ghost.border ? `1px solid ${setting.borderColor}` : 'none'}`, boxShadow:`${setting.ghost.shadow ? '2px 2px 5px rgba(0,0,0,0.3)' : 'none'}`, borderRadius:`${setting.ghost.borderRadius}px`, backgroundColor:`${setting.ghost.backgroundColor}`}}>
                                         
                                    </div>
                                </div>
                            </div>
                        }>
                            <RadioCustom button text="" options={shapeOptions} value={setting.ghost.borderRadius} func={(e) => setSetting(produce(setting, draft => {
                                draft.ghost.borderRadius = e
                            }))} />
                            <div className="edit-element">
                            <SingleColorCustom text=" " value={setting.ghost.backgroundColor} func={(e) => setSetting(produce(setting, draft => {
                                draft.ghost.backgroundColor = e;
                            }))} />
                            <div>
                            <div className="edit-element-bar"/>
                            </div>
                            <SingleColorCustom text=" " value={setting.ghost.color} func={(e) => setSetting(produce(setting, draft => {
                                draft.ghost.color = e;
                            }))} />
                            </div>
                            <OnOffCustom text="" value={setting.ghost.shadow} func={(e) => setSetting(produce(setting, draft => {
                                draft.ghost.shadow = !setting.ghost.shadow
                            }))} />
                            <OnOffCustom text="" value={setting.ghost.border} func={(e) => setSetting(produce(setting, draft => {
                                draft.ghost.border = !setting.ghost.border
                            }))} />
                            {
                                setting.ghost.border && 
                                <ColorCustom text=" " value={setting.ghost.borderColor} func={(e) => setSetting(produce(setting, draft => {
                                    draft.ghost.borderColor = e;
                                }))} />
                            }
                        </OpenCloseCustom>
                    </div>
                    <OpenCloseCustom title="" use={true}>
                        <AnimationCustom 
                            text="" 
                            options={animationOptions} 
                            value={setting.animation} 
                            func={ e => changeAnimationOption(e)} />
                    </OpenCloseCustom>
                    </>
                )
        }
    }

    return(
        <>
            {returnTable()}
        </>
    )
}

export default EdtiSetting
